import UniSInput from '../field/input.vue'

export default {
	components: {
		'uni-s-input': UniSInput
	},
	name: 'input-dialog',
	props: {
		// 是否显示弹出框
		status: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			isShow: false
		}
	},
	watch: {
		show(val) {
			this.isShow = val
		}
	},
	methods: {
		// 禁止穿透
		bindTouchmove() {},

		show() {
			this.isShow = true;
		},

		addChannel() {
			this.$emit('to-new-channel')
		},

		// 关闭弹窗
		closeDialog() {
			this.isShow = false;
			this.$emit('close')
		}
	}
}
